import React, { useEffect, useState } from "react";
import { GatsbyImage, IGatsbyImageData } from "gatsby-plugin-image";

import Border from "../../assets/common/border.svg";

import ContentContainer from "./contentContainer";
import Babylon from "../babylonjs/babylon";

type TBannerCommonProps = {
  heightClass?: string;
  title: string;
  className?: string;
  childrenBlockClassName?: string;
  borderBottom?: boolean;
  borderTop?: boolean;
};
type TBannerConditionalProps =
  | {
      babylon?: boolean;
      image?: never;
      children?: never;
      titleBlockClassName?: never;
    }
  | {
      babylon?: never;
      image?: IGatsbyImageData;
      children?: never;
      titleBlockClassName?: never;
    }
  | {
      babylon?: never;
      image?: never;
      children?: React.ReactNode;
      titleBlockClassName?: string;
    };
type TBannerProps = TBannerCommonProps & TBannerConditionalProps;

const Banner = ({
  heightClass = "lg:min-h-[396px]",
  title,
  className,
  borderBottom,
  borderTop,
  babylon,
  image,
  titleBlockClassName,
  childrenBlockClassName,
  children,
}: TBannerProps) => {
  const [textTranslate, setTextTranslate] = useState<
    "translate-y-full" | "translate-y-0"
  >("translate-y-full");
  useEffect(() => {
    setTextTranslate("translate-y-0");
  }, []);

  return (
    <div
      className={`w-full flex items-center relative  lg:overflow-hidden ${heightClass} ${className}`}
    >
      {borderTop ? (
        <Border className="absolute top-0 left-0 right-0 w-full" />
      ) : null}
      <ContentContainer
        className={`h-full flex flex-col lg:flex-row py-12 lg:py-0 items-center justify-between text-secondary-100 flex-grow gap-y-8`}
      >
        <div
          className={`flex flex-col overflow-hidden items-left h-full justify-center text-secondary-100 h-full w-full ${
            titleBlockClassName ? titleBlockClassName : "lg:w-1/2"
          }`}
        >
          <h1 className={`overflow-hidden w-full`}>
            <span
              className={`banner-title inline-block text-3xl lg:text-4xl w-full text-center transition-all duration-1000 font-light ${textTranslate}`}
              dangerouslySetInnerHTML={{ __html: `${title}` }}
            />
          </h1>
        </div>
        {babylon ? (
          <div className="w-[85%] lg:w-1/2 lg:h-full hidden md:flex -order-1 lg:order-none">
            <Babylon />
          </div>
        ) : null}
        {image ? (
          <div className="w-[220px] lg:w-1/2 lg:h-full flex justify-center lg:justify-end items-center -order-1 lg:order-none">
            <GatsbyImage
              image={image}
              alt="cover"
              className="object-contain aspect-square"
            />
          </div>
        ) : null}
        {children ? (
          <div
            className={`w-full lg:h-full flex justify-center lg:justify-start items-center ${childrenBlockClassName}`}
          >
            {children}
          </div>
        ) : null}
      </ContentContainer>
      {borderBottom ? (
        <Border className="absolute bottom-0 left-0 right-0 w-full" />
      ) : null}
    </div>
  );
};

export default Banner;
