import React from "react";
import { HeadFC, HeadProps, PageProps, graphql } from "gatsby";

import {
  IBlogByAuthorPageContext,
  IBlogByAuthorPageData,
} from "../types/blog.type";
import { Theme } from "../types/common.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import PostPreview from "../components/posts/postPreview";
import Pagination from "../components/common/pagination";
import { HeadSeo } from "../components/common/metadata";
import PostAuthorCard from "../components/posts/postAuthorCard";

const BlogByAuthor = ({
  data,
  pageContext,
}: PageProps<IBlogByAuthorPageData, IBlogByAuthorPageContext>) => {
  const posts = data.posts.nodes;
  const {
    authorAlias,
    author,
    authorPhoto,
    authorPosition,
    authorDescription,
  } = data.author.frontmatter;
  const { currentPage, pagesQuantity, markupData, lang } = pageContext;

  return (
    <>
      <Banner
        heightClass="lg:h-99"
        titleBlockClassName="lg:w-72 shrink-0 lg:mr-28"
        title={markupData.blogByAuthorBannerTitle.toUpperCase()}
        borderBottom
      >
        <div className="text-secondary-30 text-lg">
          <PostAuthorCard
            authorAlias={authorAlias}
            author={author}
            authorPhoto={authorPhoto}
            authorPosition={authorPosition}
          />
          <p className="w-full">{authorDescription}</p>
        </div>
      </Banner>
      <ContentContainer className="flex-col items-center p-6 md:p-12 gap-14">
        {posts
          ? posts.map((post) => (
              <PostPreview
                theme={Theme.dark}
                key={post.id}
                post={post}
                markupData={markupData}
                lang={lang}
              />
            ))
          : null}
        <Pagination
          section={`blog/${authorAlias.split(" ").join("-").toLowerCase()}`}
          pagesQuantity={pagesQuantity}
          currentPage={currentPage}
          nextPageTitle={pageContext.pagination.next}
        />
      </ContentContainer>
    </>
  );
};

export default BlogByAuthor;

export const Head: HeadFC<IBlogByAuthorPageData, IBlogByAuthorPageContext> = ({
  pageContext: { markupData, currentPage, site, lang },
  data: {
    author: {
      frontmatter: { author },
    },
  },
  location,
}: HeadProps<IBlogByAuthorPageData, IBlogByAuthorPageContext>) => {
  return (
    <HeadSeo
      title={`${markupData.head} | ${author} | ${currentPage}`}
      description={site.description}
      siteUrl={site.siteUrl}
      path={location.pathname}
      lang={lang}
    />
  );
};

export const query = graphql`
  query (
    $skip: Int!
    $limit: Int!
    $lang: String
    $authorAlias: String
    $authorId: String
  ) {
    posts: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "posts" }, language: { eq: $lang } }
        frontmatter: { authorAlias: { eq: $authorAlias } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "DD MMM YYYY", locale: $lang)
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
        wordCount {
          words
        }
      }
    }
    author: markdownRemark(id: { eq: $authorId }) {
      frontmatter {
        authorAlias
        author
        authorPosition
        authorDescription
        authorPhoto {
          childImageSharp {
            gatsbyImageData(
              width: 64
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
            )
          }
        }
      }
    }
  }
`;
