import React from "react";
import { Link } from "gatsby";

import { BsArrowUpRight } from "@react-icons/all-files/bs/BsArrowUpRight";

import { IPostPreviewQueryResult } from "../../types/post.type";
import { IBlogMarkupData } from "../../types/commonMarkup.type";
import { TLang, Theme } from "../../types/common.type";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

const PostPreview = ({
  post,
  theme,
  className,
  markupData,
  lang,
}: {
  post: IPostPreviewQueryResult;
  className?: string;
  theme: Theme;
  markupData?: IBlogMarkupData;
  lang: TLang;
}) => {
  const { date, title, text } = post.frontmatter;

  const minutes = Math.floor(post.wordCount.words / 200);
  const readingTime = minutes ? minutes : 1;

  const fontColorClass100 =
    theme === Theme.light ? "text-main-100" : "text-secondary-100";
  const fontColorClass70 =
    theme === Theme.light ? "text-main-70" : "text-secondary-70";
  const fontColorHover =
    theme === Theme.light ? "hover:text-main-10" : "hover:text-secondary-10";

  const cover = getImage(post.frontmatter.cover); 
  return (
    <Link
      to={`/blog/${post.id}`}
      className={`flex flex-col sm:flex-row w-full justify-center gap-5 md:gap-14 ${fontColorClass100} ${fontColorHover} ${className}`}
      itemScope
      itemType="https://schema.org/Article"
    >
      { cover &&
        <div className="mr-3.5">
          <GatsbyImage
            image={cover}
            alt=""
            className="object-cover rounded-full w-full aspect-square"
          />
        </div>
      }
      <div className="flex flex-col w-full max-w-[400px] gap-2.5">
        <p itemProp="dateCreated" className={`text-lg ${fontColorClass70}`}>
          {date}
        </p>
        <h2
          itemProp="headline"
          className={`text-3xl font-semibold ${fontColorClass100}`}
        >
          {title}
        </h2>
      </div>
      <div className="flex flex-col w-full max-w-[500px] gap-5">
        <p
          className={`font-normal text-base ${fontColorClass100}`}
          itemProp="abstract"
        >
          {text.split(" ").slice(0, 70).join(" ")}...
        </p>
        <div className="flex w-full items-center justify-between">
          <div className="flex items-center gap-3 text-sm font-bold">
            <span>{markupData?.readPostLink}</span>
            <BsArrowUpRight size={14} />
          </div>
          <p className={`text-sm ${fontColorClass70}`}>
            {`${readingTime} ${markupData?.readingTimeUnits} ${markupData?.readingTimeTitle}`}
          </p>
        </div>
      </div>
    </Link>
  );
};

export default PostPreview;
